import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';

const BlogList = () => {
  const [posts, setPosts] = useState([]);
  const [page, setPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);

  useEffect(() => {
    axios.get(`/api/blog?page=${page}&size=6`)
      .then(response => {
        setPosts(response.data.content);
        setTotalPages(response.data.totalPages);
      })
      .catch(error => console.error('Error fetching posts:', error));
}, [page]);

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
      {posts.map(post => (
        <motion.div
          key={post.id}
          className="card"
          initial={{ y: 50, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{ duration: 0.5 }}
        >
          <h3 className="text-xl font-semibold mb-2">{post.title}</h3>
          <div className="text-gray-600 dark:text-gray-400" dangerouslySetInnerHTML={{ __html: post.content.substring(0, 100) + '...' }} />
          <Link to={`/blog/${post.id}`} className="text-blue-500 hover:underline">Read More</Link>
        </motion.div>
      ))}
      <div className="col-span-1 md:col-span-2 flex justify-between mt-8">
        <button
          onClick={() => setPage(page - 1)}
          disabled={page === 0}
          className="px-4 py-2 bg-primary text-white rounded disabled:bg-gray-300 dark:disabled:bg-gray-600"
        >
          Previous
        </button>
        <button
          onClick={() => setPage(page + 1)}
          disabled={page >= totalPages - 1}
          className="px-4 py-2 bg-primary text-white rounded disabled:bg-gray-300 dark:disabled:bg-gray-600"
        >
          Next
        </button>
      </div>
    </div>
  );
};

export default BlogList;