import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { jwtDecode } from 'jwt-decode';
import navbarBg from '../assets/navbar-bg.png'; // Existing background image
import logo from '../assets/logo.png'; // Added logo import

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [theme, setTheme] = useState('light');
  const [currentTime, setCurrentTime] = useState(new Date());
  const [userRole, setUserRole] = useState(null);
  const token = localStorage.getItem('token');
  const navigate = useNavigate();

  // Decode JWT token to get user role
  useEffect(() => {
    if (token) {
      try {
        const decoded = jwtDecode(token);
        // Assuming the roles are stored in the token's "roles" claim
        const roles = decoded.roles || [];
        if (roles.includes('ROLE_ADMIN')) {
          setUserRole('ROLE_ADMIN');
        } else if (roles.includes('ROLE_USER')) {
          setUserRole('ROLE_USER');
        }
      } catch (error) {
        console.error('Error decoding token:', error);
        setUserRole(null);
      }
    } else {
      setUserRole(null);
    }
  }, [token]);

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('userId');
    setUserRole(null);
    navigate('/auth/login');
  };

  // Toggle mobile menu
  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  // Toggle theme and apply to document
  const toggleTheme = () => {
    const newTheme = theme === 'light' ? 'dark' : 'light';
    setTheme(newTheme);
    if (newTheme === 'dark') {
      document.documentElement.classList.add('dark');
    } else {
      document.documentElement.classList.remove('dark');
    }
  };

  // Update time every second
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  // Format date and time
  const formattedDateTime = currentTime.toLocaleString('en-US', {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    hour12: true,
  });

  return (
    <nav
      className="relative bg-cover bg-center bg-no-repeat shadow-md"
      style={{
        backgroundImage: `url(${navbarBg})`,
      }}
    >
      {/* Overlay for text readability */}
      <div className="absolute inset-0 bg-black bg-opacity-50"></div>
      <div className="container mx-auto px-4 py-4 flex justify-between items-center relative z-10">
        <Link to="/">
          <img
            src={logo}
            alt="Byte & Blog Logo"
            className="h-10 w-auto" // Adjust size as needed
          />
        </Link>
        <div className="flex items-center space-x-4">
          <div className="text-white text-sm">
            {formattedDateTime}
          </div>
          <div className="md:hidden">
            <button
              onClick={toggleMenu}
              className="text-white focus:outline-none"
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d={isOpen ? 'M6 18L18 6M6 6l12 12' : 'M4 6h16M4 12h16M4 18h16'}
                />
              </svg>
            </button>
          </div>
        </div>
        <div
          className={`md:flex md:items-center md:space-x-4 ${isOpen ? 'block' : 'hidden'}`}
        >
          <Link
            to="/"
            className="block py-2 md:py-0 text-white hover:text-secondary"
          >
            Home
          </Link>
          <Link
            to="/about"
            className="block py-2 md:py-0 text-white hover:text-secondary"
          >
            About Me
          </Link>
          <Link
            to="/portfolio"
            className="block py-2 md:py-0 text-white hover:text-secondary"
          >
            Portfolio
          </Link>
          <Link
            to="/blog"
            className="block py-2 md:py-0 text-white hover:text-secondary"
          >
            Blog
          </Link>
          <Link
            to="/contact"
            className="block py-2 md:py-0 text-white hover:text-secondary"
          >
            Contact Me
          </Link>
          {token ? (
            <>
              {/* Show "New Post" and "New Project" only for ROLE_ADMIN */}
              {userRole === 'ROLE_ADMIN' && (
                <>
                  <Link
                    to="/blog/new"
                    className="block py-2 md:py-0 text-white hover:text-secondary"
                  >
                    New Post
                  </Link>
                  <Link
                    to="/portfolio/new"
                    className="block py-2 md:py-0 text-white hover:text-secondary"
                  >
                    New Project
                  </Link>
                </>
              )}
              <button
                onClick={handleLogout}
                className="block py-2 md:py-0 text-white hover:text-secondary"
              >
                Logout
              </button>
            </>
          ) : (
            <>
              <Link
                to="/auth/login"
                className="block py-2 md:py-0 text-white hover:text-secondary"
              >
                Login
              </Link>
              <Link
                to="/auth/register"
                className="block py-2 md:py-0 text-white hover:text-secondary"
              >
                Register
              </Link>
            </>
          )}
          <button
            onClick={toggleTheme}
            className="block py-2 md:py-0 text-white hover:text-secondary"
          >
            {theme === 'light' ? 'Dark Mode' : 'Light Mode'}
          </button>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;