import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';

const PortfolioItemForm = () => {
  const [form, setForm] = useState({ title: '', description: '', projectUrl: '', category: 'web' });
  const [image, setImage] = useState(null);
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem('token');
      const userId = localStorage.getItem('userId');
      const formData = new FormData();
      formData.append('item', new Blob([JSON.stringify({ ...form, authorId: userId })], { type: 'application/json' }));
      formData.append('image', image);
      await axios.post('/api/portfolio', formData, {
        headers: { Authorization: `Bearer ${token}`, 'Content-Type': 'multipart/form-data' }
      });
      navigate('/portfolio');
    } catch (error) {
      console.error('Error creating portfolio item:', error);
    }
  };

  return (
    <motion.div
      className="container mx-auto p-4 bg-gray-100 dark:bg-gray-900"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <h2 className="text-3xl font-bold mb-4 dark:text-white">Add New Project</h2>
      <form onSubmit={handleSubmit} className="space-y-4 card">
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Title</label>
          <input
            type="text"
            value={form.title}
            onChange={(e) => setForm({ ...form, title: e.target.value })}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Description</label>
          <textarea
            value={form.description}
            onChange={(e) => setForm({ ...form, description: e.target.value })}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
            rows="4"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Project URL</label>
          <input
            type="url"
            value={form.projectUrl}
            onChange={(e) => setForm({ ...form, projectUrl: e.target.value })}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
          />
        </div>
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Category</label>
          <select
            value={form.category}
            onChange={(e) => setForm({ ...form, category: e.target.value })}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
          >
            <option value="web">Web</option>
            <option value="mobile">Mobile</option>
            <option value="other">Other</option>
          </select>
        </div>
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Image</label>
          <input
            type="file"
            onChange={(e) => setImage(e.target.files[0])}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
            accept="image/*"
            required
          />
        </div>
        <button type="submit" className="px-4 py-2 bg-primary text-white rounded hover:bg-accent">
          Save Project
        </button>
      </form>
    </motion.div>
  );
};

export default PortfolioItemForm;