import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import axios from 'axios';
import welcomeBg from '../assets/welcome.png';

const Home = () => {
  const [posts, setPosts] = useState([]);
  const [items, setItems] = useState([]);
  const [allNews, setAllNews] = useState([]); // Store all news articles
  const [displayedNews, setDisplayedNews] = useState([]); // Paginated news for display
  const [page, setPage] = useState(0);
  const [totalPages, setTotalPages] = useState(1);
  const [currentPage, setCurrentPage] = useState(1);
  const [loadingNews, setLoadingNews] = useState(false);
  const [newsError, setNewsError] = useState('');
  const pageSize = 6; // Number of articles per page

  useEffect(() => {
    axios.get(`/api/blog?page=${page}&size=6`)
      .then(response => {
        setPosts(response.data.content);
        setTotalPages(response.data.totalPages);
      })
      .catch(error => console.error('Error fetching posts:', error));
    
    axios.get('/api/portfolio')
      .then(res => setItems(res.data.slice(0, 3)))
      .catch(error => console.error('Error fetching portfolio items:', error));
  }, [page]);

  useEffect(() => {
    const fetchNews = async () => {
      setLoadingNews(true);
      setNewsError('');
      try {
        const response = await axios.get('/api/news/top-headlines'); // Removed params
        const { articles, totalResults } = response.data;
        setAllNews(articles); // Store all articles
        setTotalPages(Math.ceil(totalResults / pageSize));
      } catch (error) {
        setNewsError('Failed to fetch news: ' + (error.response?.data || error.message));
      } finally {
        setLoadingNews(false);
      }
    };
    fetchNews();
  }, []); // No dependencies since params are removed

  useEffect(() => {
    // Paginate articles on the frontend
    const start = (currentPage - 1) * pageSize;
    const end = start + pageSize;
    setDisplayedNews(allNews.slice(start, end));
  }, [currentPage, allNews]);

  const handlePageChange = (event, newPage) => {
    event.preventDefault();
    if (newPage >= 1 && newPage <= totalPages) {
      setCurrentPage(newPage);
    }
  };

  return (
    <div className="bg-gray-100 dark:bg-gray-900 min-h-screen">
      {/* Hero Section */}
      <motion.section
        className="relative bg-cover bg-center bg-no-repeat text-white py-16 sm:py-20 md:py-24 text-center h-[400px] sm:h-[500px] md:h-[600px]"
        style={{
          backgroundImage: `url(${welcomeBg})`,
        }}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1 }}
      >
        <div className="absolute inset-0 bg-black bg-opacity-75"></div>
        <div className="container mx-auto relative z-10 px-4 flex flex-col items-center justify-center h-full">
          <h1 className="text-2xl xs:text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-bold mb-4 leading-tight">
            Welcome to My Creative Space
          </h1>
          <p className="text-sm xs:text-base sm:text-lg md:text-xl lg:text-2xl mb-6 leading-relaxed">
            Discover my projects and dive into my thoughts through my blog.
          </p>
          <Link
            to="/portfolio"
            className="px-4 py-2 xs:px-5 xs:py-2.5 sm:px-6 sm:py-3 bg-secondary text-primary rounded-full font-semibold hover:bg-accent text-sm xs:text-base sm:text-lg"
          >
            Explore Portfolio
          </Link>
        </div>
      </motion.section>

      {/* Featured Blogs Section */}
      <section className="py-12 sm:py-16">
        <div className="container mx-auto px-4">
          <h2 className="text-2xl sm:text-3xl font-bold mb-6 sm:mb-8 text-center dark:text-white">Featured Blogs</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 sm:gap-8">
            {posts.map(post => (
              <motion.div
                key={post.id}
                className="card bg-white dark:bg-gray-800 shadow-lg rounded-lg p-4 sm:p-6"
                initial={{ y: 50, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ duration: 0.5 }}
              >
                <h3 className="text-lg sm:text-xl font-semibold mb-2">{post.title}</h3>
                <div className="text-gray-600 dark:text-gray-400 text-sm sm:text-base" dangerouslySetInnerHTML={{ __html: post.content.substring(0, 100) + '...' }} />
                <Link to={`/blog/${post.id}`} className="text-blue-500 hover:underline text-sm sm:text-base">Read More</Link>
              </motion.div>
            ))}
          </div>
          <div className="flex justify-center mt-6 sm:mt-8 space-x-4">
            <button
              onClick={() => setPage(page - 1)}
              disabled={page === 0}
              className="px-3 py-1 sm:px-4 sm:py-2 bg-primary text-white rounded disabled:opacity-50 hover:bg-accent text-sm sm:text-base"
            >
              Previous
            </button>
            <span className="py-1 sm:py-2 text-gray-600 dark:text-gray-400 text-sm sm:text-base">
              Page {page + 1} of {totalPages}
            </span>
            <button
              onClick={() => setPage(page + 1)}
              disabled={page + 1 === totalPages}
              className="px-3 py-1 sm:px-4 sm:py-2 bg-primary text-white rounded disabled:opacity-50 hover:bg-accent text-sm sm:text-base"
            >
              Next
            </button>
          </div>
        </div>
      </section>

      {/* Portfolio Highlights Section */}
      <section className="py-12 sm:py-16 bg-gray-200 dark:bg-gray-800">
        <div className="container mx-auto px-4">
          <h2 className="text-2xl sm:text-3xl font-bold mb-6 sm:mb-8 text-center dark:text-white">Portfolio Highlights</h2>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 sm:gap-8">
            {items.map(item => (
              <motion.div
                key={item.id}
                className="card bg-white dark:bg-gray-800 shadow-lg rounded-lg p-4 sm:p-6"
                initial={{ y: 50, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ duration: 0.5 }}
              >
                <img src={`${item.imageUrl}`} alt={item.title} className="w-full h-40 sm:h-48 object-cover rounded mb-4" />
                <h3 className="text-lg sm:text-xl font-semibold mb-2">{item.title}</h3>
                <p className="text-gray-600 dark:text-gray-400 text-sm sm:text-base mb-2">{item.description}</p>
                <a href={item.projectUrl} className="text-blue-500 hover:underline text-sm sm:text-base">View Project</a>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Top News Section */}
      <section className="py-12 sm:py-16">
        <div className="container mx-auto px-4">
          <h2 className="text-2xl sm:text-3xl font-bold mb-6 sm:mb-8 text-center dark:text-white">Top News</h2>
          {newsError && <p className="text-red-500 text-center mb-4 text-sm sm:text-base">{newsError}</p>}
          {loadingNews ? (
            <p className="text-center text-gray-600 dark:text-gray-400 text-sm sm:text-base">Loading news...</p>
          ) : (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 sm:gap-8">
              {displayedNews.map((article, index) => (
                <motion.div
                  key={index}
                  className="card bg-white dark:bg-gray-800 shadow-lg rounded-lg p-4 sm:p-6"
                  initial={{ y: 50, opacity: 0 }}
                  animate={{ y: 0, opacity: 1 }}
                  transition={{ duration: 0.5, delay: index * 0.1 }}
                >
                  {article.urlToImage && (
                    <img
                      src={article.urlToImage}
                      alt={article.title}
                      className="w-full h-40 sm:h-48 object-cover rounded mb-4"
                      onError={(e) => (e.target.style.display = 'none')}
                    />
                  )}
                  <h3 className="text-lg sm:text-xl font-semibold mb-2">{article.title}</h3>
                  <p className="text-gray-600 dark:text-gray-400 mb-4 text-sm sm:text-base">
                    {article.description ? article.description.substring(0, 100) + '...' : 'No description available'}
                  </p>
                  <a
                    href={article.url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-blue-500 hover:underline text-sm sm:text-base"
                  >
                    Read More
                  </a>
                </motion.div>
              ))}
              <div className="col-span-1 sm:col-span-2 lg:col-span-3 flex justify-center mt-6 space-x-4">
                <button
                  onClick={(e) => handlePageChange(e, currentPage - 1)}
                  disabled={currentPage === 1}
                  className="px-3 py-1 sm:px-4 sm:py-2 bg-primary text-white rounded disabled:opacity-50 hover:bg-accent text-sm sm:text-base"
                >
                  Previous
                </button>
                <span className="py-1 sm:py-2 text-gray-600 dark:text-gray-400 text-sm sm:text-base">
                  Page {currentPage} of {totalPages}
                </span>
                <button
                  onClick={(e) => handlePageChange(e, currentPage + 1)}
                  disabled={currentPage === totalPages}
                  className="px-3 py-1 sm:px-4 sm:py-2 bg-primary text-white rounded disabled:opacity-50 hover:bg-accent text-sm sm:text-base"
                >
                  Next
                </button>
              </div>
            </div>
          )}
        </div>
      </section>
    </div>
  );
};

export default Home;