import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from 'react-router-dom';
import { motion } from 'framer-motion';

const Login = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post('/api/auth/login', { username, password });
      localStorage.setItem('token', response.data.token);
      localStorage.setItem('userId', response.data.userId);
      navigate('/');
    } catch (err) {
      setError('Invalid credentials');
    }
  };

  return (
    <motion.div
      className="container mx-auto p-4 max-w-md bg-gray-100 dark:bg-gray-900"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <h2 className="text-3xl font-bold mb-4 dark:text-white">Login</h2>
      {error && <p className="text-red-500 mb-4">{error}</p>}
      <form onSubmit={handleSubmit} className="space-y-4 card">
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Username</label>
          <input
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Password</label>
          <input
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
            required
          />
        </div>
        <button type="submit" className="w-full p-2 bg-primary text-white rounded hover:bg-accent">
          Login
        </button>
      </form>
      <div className="mt-4 text-center">
        <Link
          to="/auth/forgot-password"
          className="text-blue-500 hover:underline dark:text-blue-400"
        >
          Forgot Password?
        </Link>
      </div>
    </motion.div>
  );
};

export default Login;