import React, { useState } from 'react';
import axios from 'axios';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';

const BlogPostForm = () => {
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem('token');
      const userId = localStorage.getItem('userId');
      await axios.post('/api/blog', { title, content, authorId: userId }, {
        headers: { Authorization: `Bearer ${token}` }
      });
      navigate('/blog');
    } catch (error) {
      console.error('Error creating post:', error);
    }
  };

  return (
    <motion.div
      className="container mx-auto p-4 bg-gray-100 dark:bg-gray-900"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <h2 className="text-3xl font-bold mb-4 dark:text-white">Create New Post</h2>
      <form onSubmit={handleSubmit} className="space-y-4 card">
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Title</label>
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
            required
          />
        </div>
        <div>
          <label className="block text-sm font-medium dark:text-gray-300">Content</label>
          <ReactQuill value={content} onChange={setContent} className="quill-editor dark:bg-gray-700 dark:text-gray-200" />
        </div>
        <button type="submit" className="px-4 py-2 bg-primary text-white rounded hover:bg-accent">
          Save Post
        </button>
      </form>
    </motion.div>
  );
};

export default BlogPostForm;