import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';

const BlogPostDetail = () => {
  const { id } = useParams();
  const [post, setPost] = useState(null);
  const [comments, setComments] = useState([]);
  const [newComment, setNewComment] = useState('');
  const [error, setError] = useState('');
  //const navigate = useNavigate();
  const token = localStorage.getItem('token');
  const userId = localStorage.getItem('userId');

  useEffect(() => {
    const fetchPost = async () => {
      console.log('Fetching post for ID:', id);
      console.log('API URL:', `/api/blog/${id}`);
      try {
        const response = await axios.get(`/api/blog/${id}`);
        console.log('Post response:', response.data);
        setPost(response.data);
        setError('');
      } catch (err) {
        console.error('Axios error details:', err);
        setError(err.response?.status === 404 ? 'Blog post not found' : `Failed to fetch post: ${err.message}`);
      }
    };

    const fetchComments = async () => {
      console.log('Fetching comments for post ID:', id);
      try {
        const response = await axios.get(`/api/comments/post/${id}`);
        console.log('Comments response:', response.data);
        setComments(response.data);
      } catch (err) {
        console.error('Error fetching comments:', err);
      }
    };

    fetchPost();
    fetchComments();
  }, [id]);

  const handleCommentSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post(
        '/api/comments',
        { content: newComment, authorId: userId, post: { id } },
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setNewComment('');
      setError('');
      const response = await axios.get(`/api/comments/post/${id}`);
      setComments(response.data);
    } catch (error) {
      setError(error.response?.data?.message || 'Failed to post comment');
      console.error('Error posting comment:', error);
    }
  };

  if (error) return <div className="container mx-auto p-4 text-red-500">{error}</div>;
  if (!post) return <div className="container mx-auto p-4">Loading...</div>;

  return (
    <motion.div
      className="container mx-auto p-4 bg-gray-100 dark:bg-gray-900"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <h1 className="text-3xl font-bold mb-4 dark:text-white">{post.title}</h1>
      <div className="card mb-8" dangerouslySetInnerHTML={{ __html: post.content }} />
      <h2 className="text-2xl font-semibold mb-4 dark:text-white">Comments</h2>
      {token ? (
        <form onSubmit={handleCommentSubmit} className="mb-8 space-y-4 card">
          {error && <p className="text-red-500">{error}</p>}
          <textarea
            value={newComment}
            onChange={(e) => setNewComment(e.target.value)}
            className="w-full p-2 border rounded dark:bg-gray-700 dark:text-gray-200"
            rows="4"
            placeholder="Add a comment..."
            required
          />
          <button type="submit" className="px-4 py-2 bg-primary text-white rounded hover:bg-accent">
            Post Comment
          </button>
        </form>
      ) : (
        <p className="mb-8 text-gray-600 dark:text-gray-400">
          Please <Link to="/auth/login" className="text-blue-500">login</Link> to comment.
        </p>
      )}
      <div className="space-y-4">
        {comments.map(comment => (
          <div key={comment.id} className="card">
            <p className="text-gray-600 dark:text-gray-400">{comment.content}</p>
            <p className="text-sm text-gray-500 dark:text-gray-500">
              Posted on {new Date(comment.createdAt).toLocaleDateString()}
            </p>
          </div>
        ))}
      </div>
    </motion.div>
  );
};

export default BlogPostDetail;