import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { motion } from 'framer-motion';
import profileBg from '../assets/profile.JPG'; // Assuming you have a profile background image

const PortfolioGallery = () => {
  const [items, setItems] = useState([]);
  const [filter, setFilter] = useState('all');

  useEffect(() => {
    axios.get('/api/portfolio')
      .then(response => setItems(response.data))
      .catch(error => console.error('Error fetching portfolio items:', error));
  }, []);

  const filteredItems = filter === 'all' ? items : items.filter(item => item.category === filter);

  return (
    <div className="bg-gray-100 dark:bg-gray-900 py-0">
      <div className="container mx-auto px-4">
        {/* New About Me Section (Moved to Top) */}
        <motion.section
          className="py-8"
          initial={{ opacity: 0, y: 50 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <div className="flex flex-col md:flex-row items-center justify-center gap-6">
            {/* Profile Image */}
            <div className="flex-shrink-0">
              <img
                src={profileBg}
                alt="Profile"
                className="w-48 h-48 rounded-full object-cover border-4 border-primary shadow-lg"
              />
            </div>
            {/* Text and Buttons */}
            <div className="">
              <h2 className="text-4xl font-bold text-gray-800 dark:text-white mb-2">Sunil Kumar</h2>
              <h3 className="text-2xl font-semibold text-gray-600 dark:text-gray-300 mb-4">A Bit About Me</h3>
              <p className="text-lg text-gray-600 dark:text-gray-400 mb-4 leading-relaxed max-w-md text-justify">
                I craft code like an artist paints  with purpose, structure, and just a hint of flair. As a Java developer and web app builder, I specialize in turning complex problems into clean, elegant solutions. From architecting secure backend systems with Spring Boot to connecting the dots between frontend interfaces and databases, I love breathing life into ideas through technology. Each line of code is a chance to create something meaningful  and thats what keeps me going.
              </p>
              <div className="flex flex-wrap justify-center md:justify-start gap-4">
                <a
                  href="#resume"
                  className="px-6 py-3 bg-green-500 text-white rounded-full font-semibold hover:bg-green-600 transition-colors duration-200"
                >
                  Resume
                </a>
                <a
                  href="#research"
                  className="px-6 py-3 bg-orange-500 text-white rounded-full font-semibold hover:bg-orange-600 transition-colors duration-200"
                >
                  Research
                </a>
                <a
                  href="#outreach"
                  className="px-6 py-3 bg-cyan-500 text-white rounded-full font-semibold hover:bg-cyan-600 transition-colors duration-200"
                >
                  Outreach
                </a>
                <a
                  href="#personal"
                  className="px-6 py-3 bg-gray-500 text-white rounded-full font-semibold hover:bg-gray-600 transition-colors duration-200"
                >
                  Personal
                </a>
              </div>
            </div>
          </div>
        </motion.section>
        {/* Portfolio Gallery Section */}
        <motion.h1
          className="text-4xl font-bold mb-8 text-center text-gray-800 dark:text-white"
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.2 }}
        >
          Portfolio Gallery
        </motion.h1>
        <div className="flex justify-center mb-8 space-x-4">
          {['all', 'web', 'mobile', 'other'].map(category => (
            <button
              key={category}
              onClick={() => setFilter(category)}
              className={`px-4 py-2 rounded-full ${filter === category ? 'bg-secondary text-primary' : 'bg-gray-200 dark:bg-gray-700 text-gray-600 dark:text-gray-300'} hover:bg-accent transition-colors duration-200`}
            >
              {category.charAt(0).toUpperCase() + category.slice(1)}
            </button>
          ))}
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {filteredItems.map(item => (
            <motion.div
              key={item.id}
              className="card overflow-hidden"
              whileHover={{ scale: 1.05 }}
              transition={{ duration: 0.3 }}
            >
              <img
                src={`${item.imageUrl}`}
                alt={item.title}
                className="w-full max-w-full h-auto rounded-t-lg object-contain"
              />
              <div className="p-4">
                <h3 className="text-xl font-semibold mb-2 text-gray-800 dark:text-white">{item.title}</h3>
                <p className="text-gray-600 dark:text-gray-400 mb-4">{item.description}</p>
                <a
                  href={item.projectUrl}
                  className="text-blue-500 dark:text-white hover:underline"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  View Project
                </a>
              </div>
            </motion.div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default PortfolioGallery;